import * as styles from "../styles/index.module.css";
import { StaticImage } from "gatsby-plugin-image";
import * as React from "react";
import { Link } from "gatsby";
import { useSelector } from 'react-redux';
import Footer from "./footer"
import Index_Links from "./links/title_links"

const Index = () => {
    const accountInfo = useSelector((state) => state.account.accountInfo);

    if (!accountInfo) {
        return null;
    }

    return (
        <div className={styles.container}>
            <Index_Links />
            <p className={styles.please_email}>
                Email: <a href={`mailto:${accountInfo.email}`}>{accountInfo.email}</a>
                <br/><br/>Text/Call: <a href={`mailto:${accountInfo.email}`}>{accountInfo.phone}</a>
            </p>
            <Footer />
        </div>
    );
};

export default Index;
